import moment from 'moment';
import React from 'react';

import cls from './ComponentModuleCard.module.scss';
import { edit } from '../../../assets/images/icons';
import { isMobile } from 'react-device-detect';

function ComponentModuleCard(props) {
	const { component, options, openModal = () => {}, setComponentToEdit = () => {}, clicked = () => {}, isEditable = true, children } = props;
	const { title } = component;

	const status = component?.status ?? component.component_fields?.find(f => f.data_type_name === 'Status')?.value ?? '';
	const image = component.component_fields?.find(f => f?.value?.includes('jpg') || f?.value?.includes('jpeg') || f?.value?.includes('png') ||  f?.value?.includes('pdf'))?.value ?? '';

	return options?.asGraphical == true ? (
		<div className={cls['component-card']} style={{ display: status === 'Hide' || status === 'Closed' || status === 'Done' ? 'none' : '', border: '0px dashed var(--app-color-gray-dark)' }}>
			<div onClick={clicked} className={cls['component-card-container']}>
				{image !== '' ? (
					image.includes('pdf') ? (
						<embed src={`${image}#toolbar=0&navpanes=0&scrollbar=0&zoom=100%`} width="100%" height="1000px" />
					) : (
						<img alt="lg" src={image} onError={(e)=> e.target.src = 'https://i.quotev.com/b2gtjqawaaaa.jpg'} style={{ margin: '0 0 0 0', padding: '3px 3px 3px 0', width: '100%', height: '100%' }} onClick={clicked} />
					)
				) : (
					<div>Image Not available</div>
				)}
			</div>
			
		</div>
	) : (
		<div className={cls['component-card']} onClick={clicked}>
			<div className={cls['component-card-container']} style={{fontWeight:'bold', color: 'var(--app-color-gray-dark)'}}>
				{image !== '' ? (
					<div style={{ width: '50%', display: 'flex' }}>
						<img alt="lg" src={image} style={{ margin: '0 0 0 0', padding: '3px 13px 3px 0', width: '50px', height: '50px' }} />
						<p className={cls.clickLabel1} onClick={clicked}>{`${title ?? component.id}`}</p>
					</div>
				) : isMobile ? (
					<p className={cls.clickLabel1} onClick={clicked}>{`${title ?? component.id}`}</p>
				) : (
					<div style={{ width: '50%', display: 'flex' }}>

						{title.split('  ').length > 0 && title.split('  ').map((t, index) => <p key={index} className={title.split('  ').length > 1 ? cls.clickLabel1 : cls.clickLabel4} onClick={clicked}>{`${t}`}</p>)}
					</div>
				)}
				{!isMobile && (
					<p className={cls.clickLabel2} onClick={clicked}>
						{status}
					</p>
				)}
				{!isMobile && (
					<p className={cls.clickLabel3} onClick={clicked}>
						{moment(component.created_date).format('DD MMM YYYY')}
					</p>
				)}
				{children}
				{isEditable && (
					<img
						alt=""
						style={{ cursor: 'pointer' }}
						src={edit}
						onClick={() => {
							openModal();
							setComponentToEdit();
						}}
					/>
				)}
			</div>
		</div>
	);
}

export default ComponentModuleCard;
